.container {
  min-height: 100vh;
  min-width: 100vw;
  position: relative;
  z-index: 2;
  background-color: white;

  >:nth-child(2) {
    width: 40px;
    height: 2px;
    background-color: #ef4b6c;
    margin: 2em auto;
  }

  >h1 {
    margin: 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 40px;
    line-height: 1;
    //letter-spacing: -1px;
    text-align: center;
    color: #333;
    position: relative;


    @media (min-width: 1200px) {
      font-size: 105px;
    }
    @media (min-width: 768px) {
      font-size: 80px;
    }
  }
  p {
     color: #757c98;
     text-align: center;
   }
}

