.aboutSection{
    min-height: 100vh;
    min-width: 100vw;
    background-color: white;
    z-index: 3;

    >:nth-child(2) {
        width: 40px;
        height: 2px;
        background-color: #ef4b6c;
        margin: 2em auto;
    }

    >h1 {
        margin: 0;
        letter-spacing: -1px;
        font-family: Playfair Display, serif;
        font-weight: 700;
        font-size: 40px;
        line-height: 1;
        //letter-spacing: -1px;
        text-align: center;
        color: #333;
        position: relative;


        @media (min-width: 1200px) {
            font-size: 105px;
        }
        @media (min-width: 768px) {
            font-size: 80px;
        }
    }


    .services {
        display: flex;
        flex-direction: row;
        flex-flow: wrap;
        margin: 0 auto;
        width: fit-content;
    }
    .service {
        width: 124px;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 0 0.8em;

        >:first-child {
            border-radius: 50%;
            width: 80px;
            height: 80px;
            margin-bottom: 1em;
        }
        >:nth-child(2) {
            text-align: center;
            h3 {
                margin: 0;
                font-weight: 600;
                font-size: 18px;
                line-height: 1.22;
                letter-spacing: 1px;
                color: #1b2025;
            }
            p {
                font-family: Lora,serif;
                font-size: 12pt;
                line-height: 1.73;
                letter-spacing: .5px;
                color: #626881;
                margin: 0;
            }
        }
    }

    .floatingTech {
        position: relative;
        width: 500px;
        height: 500px;


        :first-child {
            top: 100px;
            left: 150px;
            position: absolute;
            width: 200px;
            height: 200px;
        }
        :nth-child(2) {
            position: absolute;
            top: 0;
            left: 0;
        }
    }

    .whoisthisguy {
        padding: 20px;
        width: 500px;
        height: 500px;
        //border-left: 1px solid #cccccc;

        >h3 {
            margin-top: 0;
        }

        p {
            color: #757c98;
        }

        .link {
            color: #e31b6d;
            text-decoration: none;

            &:hover {
                text-decoration: underline !important;
                color: adjust-color(#e31b6d, $lightness: 20%, $alpha: 0.8);
            }
        }

    }

    .info {
        display: flex;
        flex-direction: row;
        flex-flow: wrap;
        //border: 1px solid #cccccc;
        //border-radius: 15px;
        width: fit-content;
        margin: 0 auto;
    }
}