p, ul {
  margin: 10px 0;
}

.duration {
  position: absolute;
  top: -20px;
  right: 15px;
  flex-direction: row;
  display: flex;
  align-items: center;

  > :nth-child(2), > :nth-child(4), > :nth-child(1) {
    background-color: white;
  }

  > :nth-child(1) {
    left: -215px;
    position: absolute;
  }

  > img {
    margin: 0 0.5em;
  }
}

.keywords {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 10px 0 0 0;


  > img {
    margin-right: 8px;
    width: 30px;
    height: 30px;
  }

  > div {
    margin-right: 8px;
  }
}

.tasks {
  font-size: 16px;
  padding: 0 0 0 10px;

  li {
    list-style-type: circle;
    margin-bottom: 6px;
    list-style-position: inside;


  }
}

.textSectionInfo {
  color: slategrey;
  margin: 10px 0 0 0;
}

.learnMoreLink, .role, .keywords, .description {
  margin-left: 10px;
}

.description {
  font-size: 16px;
}

.projectName {
  display: flex;
  flex-direction: row;

  > h3 {
    margin: 0;
    font-size: 25px;
    letter-spacing: 1.25px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    line-height: 1.33;
    color: #1b2025;
  }

}

.projectInformationContainer {
  width: 359px;
  padding: 20px;
  color: #757c98;
}

.projectImageContainer {
  width: 399px;
  height: 100% !important;
}

.project {
  position: relative;
  margin: 1em auto;
  width: 800px;
  min-height: 400px;
  border: 1px solid #eaeaea;
  border-radius: 15px;
  box-shadow: 0 10px 40px -10px rgba(0, 64, 128, .1);
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
}


.learnMoreLink {
  display: flex;
  flex-direction: row;

  p {
    margin: 0;
    font-size: 16px;
  }

}

.link {
  color: #e31b6d;
  text-decoration: none;

  &:hover {
    text-decoration: underline !important;
    color: adjust-color(#e31b6d, $lightness: 20%, $alpha: 0.8);
  }
}

a {
  margin: 0 !important;
}