.container {
  min-height: 100vh;
  min-width: 100vw;
  //background-image: linear-gradient(to top right, #3629a7, #343a40);
  position: relative;
  z-index: 2;

  form {
    z-index: 2;
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: white;
    width: 300px;
    height: fit-content;
    top: 20%;
    left: 8.5%;
    border-radius: 10px;
    box-shadow: 0 70px 140px 0 rgba(130, 136, 171, .2);
    padding: 25px 40px 40px 40px;

    @media (max-width: 431px) {
      width: calc(90% - 80px);
      left: 5%;
    }

    h3 {
      margin: 0;
      font-family: Playfair Display, serif;
      font-size: 45px;
    }

    .subtitle {
      font-size: 12px;
      line-height: 2;
      color: #757c98;
    }
  }

  img {
    top: 0;
    position: absolute;
    z-index: 3;
    left: 50%;
    width: 100%;
    transform: translateX(-50%);
    display: block;
  }
}