.homeSection {
  height: 100vh;
  width: 100vw;
  /*overflow: hidden;*/

}

.particles {
  background-image: linear-gradient(to top right, #3629a7, #343a40);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro {

  font-size: 32pt;
  line-height: 36pt;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

}

.line {
  width: 1120px;
  background-color: #BBD336;
  height: 3px;
  padding: 0;
}


.heroText {

  :first-child {
    //color: rgb(211, 54, 83);
    background-image: linear-gradient(to right top, #f50057, #ffc107);
  }

  :nth-child(3) {
    //color: rgb(211, 131, 56);
    background-image: linear-gradient(to right top, #f50057, #ffc107);
    height: 100px;

  }

  //border: 1px solid;
  position: absolute;
  top: 150px;
  left: calc((100% - 1174px) / 2);
  z-index: 2;

  h1 {
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 55px;
    line-height: 1;
    //letter-spacing: -1px;
    text-align: center;
    color: #fff;

    @media (min-width: 1200px) {
      font-size: 105px;
    }
    @media (min-width: 768px) {
      font-size: 80px;
    }
  }
}

.HeaderWhiteMask {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 2;


  img {
    bottom: 0;
    position: absolute;
    z-index: 1;
    left: 50%;
    width: 727px;
    transform: translateX(-50%);
  }

  //:first-child {
  //  top: 0;
  //}

  @media (min-width: 768px) {
    width: 1340px;
  }

  @media (min-width: 1200px) {
    width: 1920px;
  }
}

.HeaderWhiteMask img {
  display: block;
  width: 100%;
}

@media screen and (max-width: 759px) {
  .intro-text {
    font-size: 18pt;
  }
  .line {
    width: 360px;
  }
}


@media screen and (max-width: 400px) {
  .intro-text {
    font-size: 16pt;
  }
  .line {
    width: 260px;
  }
}

@media screen and (max-width: 300px) {
  .intro-text {
    font-size: 16pt;
    overflow: hidden;
  }
  .line {
    width: 100%;
  }
}

.highlight {
  color: #e31b6d;
  font-family: "Raleway-semibold";
}