.container {
  display: flex;
  flex-direction: column;

}
.navBar {
  display: flex;
  position: fixed;
  top: 10px;
  right: 200px;
  color: white;
  z-index: 5;
  flex-direction: row;

  a {
    text-decoration: none;

    font-size: 14pt;
    margin-right: 5px;
    padding: 0 6px;
    color: #ccc;


    &:hover {
      color: #fff;
    }
    button {
      z-index: 2;
      //background-color: white;
      font-weight: bold;
      border: 2px solid;
      &:hover {
        border: 2px solid;
      }
    }
  }
}
